<template>
  <header>
    <h2 class="h2 article-title">Resumen</h2>
  </header>

  <section class="timeline">

    <div class="title-wrapper">
      <div class="icon-box">
        <ion-icon name="book-outline"></ion-icon>
      </div>

      <h3 class="h3">Educación</h3>
    </div>

    <ol class="timeline-list" v-if="resumenInfo">

      <li class="timeline-item" v-for="(resumen, index) in resumenInfo" :key="index">

        <h4 class="h4 timeline-item-title">{{ resumen.attributes.Institucion }}</h4>

        <span>{{ resumen.attributes.fechaEdu }}</span>

        <p class="timeline-text">
          {{ resumen.attributes.Descripcion }}
        </p>

      </li>

    </ol>

  </section>

  <section class="timeline">

    <div class="title-wrapper">
      <div class="icon-box">
        <ion-icon name="book-outline"></ion-icon>
      </div>

      <h3 class="h3">Experiencia</h3>
    </div>

    <ol class="timeline-list" v-if="experienciaInfo">

      <li class="timeline-item" v-for="(experiencia, index) in experienciaInfo" :key="index">

        <h4 class="h4 timeline-item-title">{{ experiencia.attributes.Empresa }}</h4>
        <p class="timeline-text">{{ experiencia.attributes.Cargo }}</p>

        <span>{{ experiencia.attributes.FechaInicio }} - {{ experiencia.attributes.FechaFin }}</span>

        <p class="timeline-text">
          {{ experiencia.attributes.Descripcion }}
        </p>

      </li>

    </ol>

  </section>

  <section class="skill">

    <h3 class="h3 skills-title">Mis Habilidades</h3>

    <ul class="skills-list content-card" v-if="abilidadInfo">

      <li class="skills-item" v-for="(abilidad, index) in abilidadInfo" :key="index">

        <div class="title-wrapper">
          <h5 class="h5">{{ abilidad.attributes.Habilidad }}</h5>
          <data :value="abilidad.attributes.porcentaje">{{ abilidad.attributes.porcentaje }}%</data>
        </div>

        <div class="skill-progress-bg">
          <div class="skill-progress-fill" :style="'width: ' + abilidad.attributes.porcentaje + '%'"></div>
        </div>

      </li>

    </ul>

  </section>
</template>

<script>
import { getResumenApi,getExperienciaApi,getAbilidadApi } from "@/api/datosCv";
export default {
  name: 'resume',
  props: {
    msg: String
  },
  data() {
        return {
            resumenInfo: null,
            experienciaInfo: null,
            abilidadInfo: null,
        };
    },
    async created() {
        try {
            const resumenResult = await getResumenApi();
            this.resumenInfo = JSON.parse(JSON.stringify(resumenResult.data));
            /* console.log(result); */
        } catch (error) {
            console.error(error);
        }
        try {
            const experienciaResult = await getExperienciaApi();
            this.experienciaInfo = JSON.parse(JSON.stringify(experienciaResult.data));
            /* console.log('Experiencia:', this.experienciaInfo); */
        } catch (error) {
            console.error(error);
        }
        try {
            const abilidadResult = await getAbilidadApi();
            this.abilidadInfo = JSON.parse(JSON.stringify(abilidadResult.data));
            /* console.log(this.abilidadInfo); */
        } catch (error) {
            console.error(error);
        }
    },
}
</script>

<style></style>