import { API_URL } from "@/utils/constants";

export async function getDatosCvApi(){
    try {
        const response = await fetch(`${API_URL}/api/datoes?populate=*`);
        const result = await response.json();
        /* console.log(result); */
        return result;
    } catch (error) {
        console.log(error);
        return null;
    }
}
