<template>
    <div class="sidebar-info" v-if="personalInfo">

        <figure class="avatar-box">
            <img class="userImg" :src="getAvatarUrl()" alt="Profile Avatar" width="80">
        </figure>

        <div class="info-content">
            <h1 class="name" :title="personalInfo.attributes.Nombre">
                {{ personalInfo.attributes.Nombre }}
            </h1>
            <h5 class="title1">Técnico en</h5>
            <p class="title">{{ personalInfo.attributes.Ocupacion }}</p>
        </div>

        <button class="info_more-btn" data-sidebar-btn>
            <span>Show Contacts</span>

            <ion-icon name="chevron-down" ></ion-icon>
        </button>

    </div>

    <div class="sidebar-info_more" v-if="personalInfo">

        <div class="separator"></div>

        <ul class="contacts-list">

            <li class="contact-item">

                <div class="icon-box">
                    <ion-icon name="mail-outline"></ion-icon>
                </div>

                <div class="contact-info">
                    <p class="contact-title">Email</p>

                    <a href="mailto:yoelfh101@gmail.com" class="contact-link">{{ personalInfo.attributes.Correo }}</a>
                </div>

            </li>

            <li class="contact-item">

                <div class="icon-box">
                    <ion-icon name="phone-portrait-outline"></ion-icon>
                </div>

                <div class="contact-info">
                    <p class="contact-title">Telefono</p>

                    <a href="tel:+12133522795" class="contact-link">+51 {{ personalInfo.attributes.Telefono }}</a>
                </div>

            </li>

            <li class="contact-item">

                <div class="icon-box">
                    <ion-icon name="calendar-outline"></ion-icon>
                </div>

                <div class="contact-info">
                    <p class="contact-title">Fecha de Cumpleaños</p>

                    <time datetime="1982-06-23">17 noviembre 1993</time>
                </div>

            </li>

            <li class="contact-item">

                <div class="icon-box">
                    <ion-icon name="location-outline"></ion-icon>
                </div>

                <div class="contact-info">
                    <p class="contact-title">Ubicación</p>

                    <address>Cerrito de la Libertad - Huancayo</address>
                </div>

            </li>

        </ul>

        <div class="separator"></div>

        <ul class="social-list">

            <li class="social-item">
                <a href="#" class="social-link">
                    <ion-icon name="logo-facebook"></ion-icon>
                </a>
            </li>

            <li class="social-item">
                <a href="#" class="social-link">
                    <ion-icon name="logo-twitter"></ion-icon>
                </a>
            </li>

            <li class="social-item">
                <a href="#" class="social-link">
                    <ion-icon name="logo-instagram"></ion-icon>
                </a>
            </li>

        </ul>

    </div>
</template>

<script>
import { getDatosCvApi } from "@/api/datosCv";
import { API_URL } from "@/utils/constants";

export default {
    name: "aside",
    props: {
        msg: String,
    },
    data() {
        return {
            personalInfo: null,
        };
    },
    async created() {
        try {
            const result = await getDatosCvApi();
            this.personalInfo = result.data[0];
            /* console.log(this.personalInfo); */
        } catch (error) {
            console.error(error);
        }
    },
    methods: {
        getAvatarUrl() {
            if (
                this.personalInfo &&
                this.personalInfo.attributes.fotoCV &&
                this.personalInfo.attributes.fotoCV.data.attributes.url
            ) {
                const imageUrl = this.personalInfo.attributes.fotoCV.data.attributes.url;
                return API_URL + imageUrl; // Construye la URL completa de la imagen
            }
            return "@/assets/images/my-avatar.png"; // URL de avatar por defecto
        },
    },
};
</script>

<style>
.userImg {
    border-radius: 20px;
}
.title1{
  color: var(--white-1);
  font-size: 12px;
  font-weight: var(--fw-800);
  letter-spacing: -0.25px;
  text-align: center;
}
.name{
text-align: justify;
}
</style>