<template>
  <ul class="navbar-list">
    <li class="navbar-item">
      <button class="navbar-link" :class="{ active: selectedPage === 'about' }" @click="changePage('about')">About</button>
    </li>
    <li class="navbar-item">
      <button class="navbar-link" :class="{ active: selectedPage === 'resume' }" @click="changePage('resume')">Resume</button>
    </li>
    <li class="navbar-item">
      <button class="navbar-link" :class="{ active: selectedPage === 'portfolio' }" @click="changePage('portfolio')">Portfolio</button>
    </li>
    <li class="navbar-item">
      <button class="navbar-link" :class="{ active: selectedPage === 'blog' }" @click="changePage('blog')">Blog</button>
    </li>
    <li class="navbar-item">
      <button class="navbar-link" :class="{ active: selectedPage === 'contact' }" @click="changePage('contact')">Contact</button>
    </li>
  </ul>
</template>

<script>
export default {
    name: 'navBar',
    props: {
        msg: String
    },
    methods: {
    changePage(page) {
        this.$emit('page-change', page);
        this.selectedPage = page; // Actualiza la página seleccionada
    }
  }
}


</script>

<style>
.navbar-link.active {
  color: #fdd96f;
}
</style>